@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap');
.chat-div{
	height: 100vh;
	overflow:hidden;
	background-color: pink;
	font-family: 'Comfortaa', cursive;
}
.chat-container{
	height: 70%;
	width: 100%;
	margin-top: 200px;

}
.chat-body{
	width: 70%;
	height: 98%;
	margin-left: auto;
	margin-right: auto;
	border-radius: 20px;
	box-shadow: 0 0 5px #999;
	background-color: white;
}
.chat-messageBox-div{
	width: 100%;
	height: 75%;
	overflow-y: auto;
	background-repeat: no-repeat;
	background-position: center bottom;

}

.chat-messageBox-div::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: rgba(0, 0, 0, 0.11);	
}

.chat-messageBox-div::-webkit-scrollbar
{
	width: 6px;
	background-color:rgba(0, 0, 0, 0.123);	
}

.chat-messageBox-div::-webkit-scrollbar-thumb
{
	background-color: #dbdbdb;
}


.chat-messageBox{
	min-height: 100%;
	background-color: rgba(0, 0, 0, 0.281);	
}


.respond{
	position: absolute;
	z-index: 10;
	height: 500px;
	width: 350px;
	top:400px;
	background-color: white;
	left:50%;
	border-radius: 10px;
	transform: translate(-50%,-50%);
	box-shadow: 
    0 6px 15px 2px rgba(0,0,0,0.5);
	transition: .3s linear;
}
#caller{
	margin-left: auto;
	margin-right: auto;
	margin-top: 10px;
	width: 190px;
	height: 190px;
}
.description{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	min-height: 90px;
	margin-top: auto;
	margin-bottom: auto;
}
.choice{
	width: 100%;;
	display: flex;
	justify-content: center;
	align-items: center;
}
#choice-btn{
	display: flex;
	margin-right: 100px;
	width: 80px;
	height: 80px;
	background-color: rgb(82, 209, 82);
	color: whitesmoke;
}
#choice-canc{
	color: whitesmoke;
	background-color: red;
	width: 80px;
	height: 80px;
}